.landing-page {
  background-color: black;
}

.page-header {
	background-image: url('./img/electricity-cip-cut.jpg');
}

.styled-div_1 {
	display: block;
	margin: 0;
	width: 100%;
	height: 15em;
	background-image:url('./img/electric-fix-2.jpg');
}

.styled-div_2 {
	display: block;
	margin: 0;
	width: 100%;
	height: 15em;
	background-image:url('./img/electric-2.jpg');
}

.about-us-pic {
	width: 300px;
	height: auto;
	background-image:url('./img/electrician-sky.jpg');
	border-radius: 6px;
	-webkit-box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.12), 0 1px 5px 0 rgba(0, 0, 0, 0.2);
	        box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.12), 0 1px 5px 0 rgba(0, 0, 0, 0.2);
}

.about-us-pic::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
	background-image: -webkit-linear-gradient(330deg, #152a3f, #001c35);
	background-image: -o-linear-gradient(330deg, #152a3f, #001c35);
	background-image: linear-gradient(120deg, #152a3f, #001c35);
	mix-blend-mode: hue;
	opacity: .8;
	z-index: 1;
}

.banner > * {
    z-index: 100;
}

.about-us {
	margin-top: 3rem;
}

.features {
	margin-top: 7rem;
	margin-bottom: -5rem;
}

.features, h2.title {
	margin-bottom: -3rem;
}

.section-contacts, h2.title {
	margin-bottom: 3rem;
}

.section text-center, h2.title {
	align: right;
}

.cover-title {
	font-size: 4em;
	color: #f5f5f5 !important;
}

.cover-subtitle {
	font-size: 1.6em;
	font-weight: 400;
	color: #f8f8f8;
}


